<template>
  <div class="shortcutContent" :style="{ left: left + 'px', top: top + 'px' }" v-if="contextmenuShow">
    <div class="shortcut" v-for="item in newShortcutList" :key="item.text" @click="item.onClick()">
      <div class="shortcutItem divider" v-if="item.text === '展开所有下级节点'">
        <span>{{ item.text }}</span>
        <span>{{ item.shortcut }}</span>
      </div>
      <div class="shortcutItem" v-else-if="item.text === '收起所有下级节点'">
        <span>{{ item.text }}</span>
        <span>{{ item.shortcut }}</span>
      </div>
      <div class="shortcutItem divider" v-else-if="item.text === '上移节点'">
        <span>{{ item.text }}</span>
        <span>{{ item.shortcut }}</span>
      </div>
      <div class="shortcutItem divider" style="color: #f56c6c" v-else-if="item.text === '删除所有下级节点'">
        <span>{{ item.text }}</span>
        <span>{{ item.shortcut }}</span>
      </div>
      <div class="shortcutItem" style="color: #f56c6c" v-else-if="item.text === '仅删除当前节点'">
        <span>{{ item.text }}</span>
        <span>{{ item.shortcut }}</span>
      </div>
      <div class="shortcutItem" v-else>
        <span>{{ item.text }}</span>
        <span>{{ item.shortcut }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      contextmenuShow: false,
      currentNode: null,
      left: 0,
      top: 0,
      shortcutList: [
        {
          text: '插入下级节点',
          shortcut: 'Tab | Insert',
          onClick: () => {
            this.$parent.mindMap.execCommand('INSERT_CHILD_NODE')
            this.contextmenuShow = false
          },
        },
        {
          text: '插入同级节点',
          shortcut: 'Enter',
          onClick: () => {
            this.$parent.mindMap.execCommand('INSERT_NODE')
            this.contextmenuShow = false
          },
        },
        {
          text: '插入父节点',
          shortcut: 'Shift + Tab',
          onClick: () => {
            this.$parent.mindMap.execCommand('INSERT_PARENT_NODE')
            this.contextmenuShow = false
          },
        },
        {
          text: '展开所有下级节点',
          shortcut: '/',
          onClick: () => {
            this.$parent.mindMap.execCommand('EXPAND_ALL', this.currentNode.uid)
            this.contextmenuShow = false
          },
        },
        {
          text: '收起所有下级节点',
          shortcut: '/',
          onClick: () => {
            this.$parent.mindMap.execCommand('UNEXPAND_ALL', false, this.currentNode.uid)
            this.contextmenuShow = false
          },
        },
        {
          text: '上移节点',
          shortcut: 'Delete | ↑',
          onClick: () => {
            this.$parent.mindMap.execCommand('UP_NODE')
            this.contextmenuShow = false
          },
        },
        {
          text: '下移节点',
          shortcut: 'Shift + ↓',
          onClick: () => {
            this.$parent.mindMap.execCommand('DOWN_NODE')
            this.contextmenuShow = false
          },
        },
        {
          text: '删除所有下级节点',
          shortcut: 'Delete | Backspace',
          onClick: () => {
            this.$parent.mindMap.execCommand('REMOVE_NODE')
            this.contextmenuShow = false
          },
        },
        {
          text: '仅删除当前节点',
          shortcut: 'Shift + Backspace',
          onClick: () => {
            this.$parent.mindMap.execCommand('REMOVE_CURRENT_NODE')
            this.contextmenuShow = false
          },
        },
        {
          text: '编辑节点',
          shortcut: 'F2',
          onClick: () => {
            this.$nextTick(() => {
              setTimeout(() => {
                this.$parent.mindMap.renderer.textEdit.show({ node: this.currentNode })
              }, 100)
            })
            this.contextmenuShow = false
          },
        },
      ],
    }
  },
  computed: {
    newShortcutList() {
      if (this.currentNode && this.currentNode.isRoot) {
        return this.shortcutList.filter((item) => item.text !== '插入同级节点' && item.text !== '插入父节点' && item.text !== '仅删除当前节点')
      } else {
        return this.shortcutList
      }
    },
  },
  methods: {
    nodeContextMenu(e, node) {
      this.contextmenuShow = true
      const menuWidth = 360 // 菜单宽度
      const menuHeight = this.shortcutList.length * 35 + 20 // 菜单高度

      // 获取屏幕宽度和高度
      const screenWidth = window.innerWidth
      const screenHeight = window.innerHeight

      // 计算菜单的位置
      let left = e.clientX
      let top = e.clientY

      // 检查菜单是否会超出屏幕右侧
      if (left + menuWidth > screenWidth) {
        left = screenWidth - menuWidth
      }

      // 检查菜单是否会超出屏幕底部
      if (top + menuHeight > screenHeight) {
        top = screenHeight - menuHeight
      }

      this.left = left
      this.top = top - 20
      this.currentNode = node
    },
  },
  created() {},
  mounted() {},
}
</script>

<style lang="scss" scoped>
.shortcutContent {
  width: 300px;
  position: fixed;
  z-index: 9999;
  box-shadow: 0px 3px 7px 0px rgba(94, 110, 136, 0.2);
  background-color: #fff;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  padding: 10px 0;
  i {
    font-size: 16px;
    margin-right: 6px;
  }
  .shortcut,
  .shortcutItem {
    width: 100%;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    user-select: none;
    .shortcutItem {
      padding: 6px 14px;
    }
    &:hover {
      background-color: #f5f5f5;
    }
    span {
      margin-left: 4px;
      cursor: pointer;
      &:last-child {
        color: #666;
      }
    }
  }
  .divider {
    border-top: 1px solid #eee;
  }
}
</style>
