<template>
  <div class="controlContainer">
    <div class="controlContent" v-for="item in newControlList" :key="item.iconClass" :title="item.title">
      <div class="controlItem" v-if="item.text === '导出'">
        <el-dropdown @command="generate">
          <div class="controlItem">
            <i :class="item.iconClass"></i>
            <span>{{ item.text }}</span>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="png">png</el-dropdown-item>
            <el-dropdown-item command="pdf">pdf</el-dropdown-item>
            <el-dropdown-item command="svg">svg</el-dropdown-item>
            <el-dropdown-item command="json">json</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div :class="['controlItem', { undoDisable: undoDisable }]" :disabled="undoDisable" @click="item.onClick" v-else-if="item.text === '上一步'">
        <i :class="item.iconClass"></i>
        <span>{{ item.text }}</span>
      </div>
      <div :class="['controlItem', { redoDisable: redoDisable }]" :disabled="redoDisable" @click="item.onClick" v-else-if="item.text === '下一步'">
        <i :class="item.iconClass"></i>
        <span>{{ item.text }}</span>
      </div>
      <div class="controlItem" @click="item.onClick" v-else>
        <i :class="item.iconClass"></i>
        <span>{{ item.text }}</span>
      </div>
    </div>
    <input type="file" ref="uploadInput" @change="handleUpload" style="display: none" accept=".json, application/json" />
  </div>
</template>
<script>
import MindMap from 'simple-mind-map'
import Export from 'simple-mind-map/src/plugins/Export.js'
MindMap.usePlugin(Export)
import ExportPDF from 'simple-mind-map/src/plugins/ExportPDF.js'
MindMap.usePlugin(ExportPDF)
export default {
  props: {
    isReadonly: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      undoDisable: true,
      redoDisable: true,
    }
  },
  computed: {
    controlList() {
      return [
        {
          iconClass: this.isReadonly ? 'el-icon-edit-outline' : 'el-icon-view',
          title: '切换预览/编辑模式',
          text: this.isReadonly ? '编辑' : '预览',
          onClick: () => {
            this.$parent.isReadonly = !this.isReadonly
            this.$parent.$refs.mindMap.mindMap.setMode(this.$parent.isReadonly ? 'readonly' : 'edit')
          },
        },
        {
          iconClass: 'el-icon-warning-outline',
          title: '键盘快捷键',
          text: '快捷键',
          onClick: () => {
            this.$parent.$refs.shortcut.isShortcut = !this.$parent.$refs.shortcut.isShortcut
          },
        },
        {
          iconClass: 'el-icon-zoom-out',
          title: '缩小图谱',
          text: '缩小',
          onClick: () => {
            this.$parent.$refs.mindMap.mindMap.view.narrow()
          },
        },
        {
          iconClass: 'el-icon-zoom-in',
          title: '放大图谱',
          text: '放大',
          onClick: () => {
            this.$parent.$refs.mindMap.mindMap.view.enlarge()
          },
        },
        {
          iconClass: 'el-icon-refresh-left',
          title: '恢复图谱原有尺寸',
          text: '恢复',
          onClick: () => {
            this.$parent.$refs.mindMap.mindMap.view.fit()
          },
        },
        {
          iconClass: 'el-icon-back',
          title: '回到上一步',
          text: '上一步',
          onClick: () => {
            this.$parent.$refs.mindMap.mindMap.execCommand('BACK')
          },
        },
        {
          iconClass: 'el-icon-right',
          title: '移到下一步',
          text: '下一步',
          onClick: () => {
            this.$parent.$refs.mindMap.mindMap.execCommand('FORWARD')
          },
        },
        {
          iconClass: 'el-icon-full-screen',
          title: '全屏查看图谱',
          text: '全屏',
          onClick: () => {
            const element = document.body
            if (element.requestFullScreen) {
              element.requestFullScreen()
            } else if (element.webkitRequestFullScreen) {
              element.webkitRequestFullScreen()
            } else if (element.mozRequestFullScreen) {
              element.mozRequestFullScreen()
            }
            setTimeout(() => {
              this.$parent.$refs.mindMap.mindMap.resize()
            }, 200)
          },
        },
        {
          iconClass: 'el-icon-download',
          title: '导出图谱',
          text: '导出',
        },
        {
          iconClass: 'el-icon-upload2',
          title: '上传图谱',
          text: '上传',
          onClick: () => {
            // 触发文件上传组件的点击事件
            this.$refs.uploadInput.click()
          },
        },
        {
          iconClass: 'el-icon-folder-checked',
          title: '保存图谱',
          text: '保存',
          onClick: () => {
            this.$parent.getMindMapData()
          },
        },
      ]
    },
    newControlList() {
      if (this.isReadonly) {
        return this.controlList.filter((item) => item.text !== '快捷键' && item.text !== '保存' && item.text !== '上传')
      } else {
        return this.controlList
      }
    },
  },
  methods: {
    //导出
    async generate(fileType) {
      const data = await this.$parent.$refs.mindMap.mindMap.doExport[fileType]()
      let byteArray
      if (typeof data === 'string' && data.startsWith('data:')) {
        // 如果是 Base64 数据，先将其转换为二进制数据
        const base64Data = data.split(',')[1]
        byteArray = Uint8Array.from(atob(base64Data), (c) => c.charCodeAt(0))
      } else {
        byteArray = data
      }

      let mimeType
      switch (fileType) {
        case 'png':
          mimeType = 'image/png'
          break
        case 'pdf':
          mimeType = 'application/pdf'
          break
        case 'svg':
          mimeType = 'image/svg+xml'
          break
        case 'json':
          mimeType = 'application/json'
          break
      }

      const blob = new Blob([byteArray], { type: mimeType })
      const element = document.createElement('a')
      element.href = URL.createObjectURL(blob)
      element.download = `mindMap-${Date.now()}.${fileType}`
      document.body.appendChild(element)
      element.click()
      document.body.removeChild(element)
      // 释放 URL 对象以节省内存
      URL.revokeObjectURL(element.href)
    },
    // 上传文件
    handleUpload(ev) {
      if (ev.target.files[0].type != 'application/json') {
        this.useMessage('error', '请上传JSON格式的文件')
        ev.target.value = '' // 清空文件输入框的值
        return
      }
      const file = ev.target.files[0]
      const reader = new FileReader()
      reader.onload = (event) => {
        if (event.target) {
          const jsonData = event.target.result
          this.$parent.$refs.mindMap.mindMap.setFullData(JSON.parse(jsonData))
          this.$parent.mindMapData = this.$parent.$refs.mindMap.mindMap.getData()
          this.useMessage('success', '文件上传成功')
          ev.target.value = '' // 清空文件输入框的值
          setTimeout(() => {
            this.$parent.$refs.mindMap.mindMap.view.fit()
          }, 100)
        }
      }
      reader.readAsText(file)
    },
  },
  created() {},
  mounted() {},
}
</script>
<style lang="scss" scoped>
i {
  font-size: 22px;
}

.redoDisable,
.undoDisable {
  opacity: 0.5;
  pointer-events: none;
}
.controlContainer {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 999;
  width: auto;
  border: none;
  height: 60px;
  box-shadow: 0px 3px 7px 0px rgba(94, 110, 136, 0.2);
  display: flex;
  align-items: center;
  justify-content: right;
  .controlContent {
    cursor: not-allowed;
    height: 100%;
    background-color: #fff;
    background-color: #fff;
    .controlItem {
      height: 100%;
      width: 50px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      color: #000;
      cursor: pointer;
      &:hover {
        background-color: #efefef;
      }
      span {
        margin-top: 5px;
        font-size: 12px;
        user-select: none;
      }
    }
  }
}
</style>
