<template>
  <div class="mindMapContainer" v-loading="loading">
    <control ref="control" :isReadonly="isReadonly" />
    <mindMap ref="mindMap" :mindMapData="mindMapData" @nodeTagClick="nodeTagClick" :isReadonly="isReadonly"/>
    <shortcut ref="shortcut" v-if="!isReadonly" />
  </div>
</template>
<script>
import control from '@/components/mindMap/control.vue'
import mindMap from '@/components/mindMap/mindMap.vue'
import shortcut from '@/components/mindMap/shortcut.vue'
import { mindMapData } from './mindMapData'
export default {
  components: {
    control,
    mindMap,
    shortcut,
  },
  data() {
    return {
      mindMapData: mindMapData,
      loading: false, // 加载状态
      isReadonly: false,
    }
  },
  methods: {
    // 获取数据
    getMindMapData() {
      this.loading = true
      setTimeout(() => {
        this.loading = false
        this.mindMapData = this.$refs.mindMap.mindMap.getData()
        localStorage.setItem('mindMapData', JSON.stringify(this.mindMapData))
        this.$message.success('保存成功')
        console.log(this.mindMapData)
      }, 1000)
    },
    // 节点标签点击事件
    nodeTagClick(item) {
      if (!this.isReadonly) {
        return
      }
      if (item.name === 'redIssuesNum' || item.name === 'orangeIssuesNum' || item.name === 'greenNum') {
        console.log(item.name)
      }
    },
  },
  created() {
    if (localStorage.getItem('mindMapData')) {
      this.mindMapData = JSON.parse(localStorage.getItem('mindMapData'))
    }
  },
  mounted() {},
}
</script>
<style lang="scss" scoped>
.mindMapContainer {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}
</style>
