import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false
Vue.prototype.useMessage = function (type, msg) {
  this.$message({
    type: type,
    message: msg,
    // offset: window.screen.height / 2.2,
    duration: 1500,
  })
}

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
// 使用 Element UI
Vue.use(ElementUI)

new Vue({
  render: (h) => h(App),
}).$mount('#app')
