<template>
  <div class="mindMapContainer">
    <div id="mindMap"></div>
    <contextmenu ref="contextmenu" v-if="!isReadonly"></contextmenu>
  </div>
</template>
<script>
import MindMap from 'simple-mind-map'
import contextmenu from './contextmenu.vue'
export default {
  components: { contextmenu },
  props: {
    isReadonly: {
      type: Boolean,
    },
    mindMapData: {
      type: Object,
    },
  },
  data() {
    return {
      mindMap: null,
    }
  },
  computed: {
    mindMapConfig() {
      return {
        alwaysShowExpandBtn: false, //是否一直显示节点的展开收起按钮，默认为鼠标移上去和激活时才显示
        mouseScaleCenterUseMousePosition: false, //鼠标缩放是否以鼠标当前位置为中心点，否则以画布中心点
        fit: true, //是否自动适配画布大小
        initRootNodePosition: ['left', 'top'], //缩放初始根节点的位置
        selectTextOnEnterEditText: true, //是否在编辑节点时选中输入框中的文字
        //自定义备注样式
        noteIcon: {
          icon: '<svg t="1740044024079" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="5860" width="200" height="200"><path d="M504.5 101.77c-226.563 0-410.229 183.666-410.229 410.229 0 226.565 183.666 410.231 410.229 410.231s410.229-183.666 410.229-410.231c0-226.563-183.666-410.229-410.229-410.229z m0 755.048c-190.437 0-344.818-154.381-344.818-344.819 0-190.438 154.381-344.818 344.818-344.818 190.438 0 344.819 154.381 344.819 344.818 0 190.439-154.381 344.819-344.819 344.819z" fill="" p-id="5861"></path><path d="M504.5 313.259m-41.998 0a41.998 41.998 0 1 0 83.996 0 41.998 41.998 0 1 0-83.996 0Z" fill="" p-id="5862"></path><path d="M546.498 712.738c0 22.092-17.908 40-40 40h-3.996c-22.091 0-40-17.908-40-40V437.255c0-22.091 17.909-40 40-40h3.996c22.092 0 40 17.909 40 40v275.483z" fill="" p-id="5863"></path></svg>',
          style: { size: 20, color: '#666' },
        },
        themeConfig: {
          lineStyle: 'curve', //连线风格
          lineColor: '#000', //连线颜色
          // 根节点样式
          root: {
            fillColor: '#fff',
            borderColor: '#000',
            borderWidth: 1,
            color: '#666',
            fontWeight: 'none',
          },
          // 二级节点样式
          second: {
            marginY: 10,
            borderColor: '#000',
            borderWidth: 1,
          },
          // 三级及以下节点样式
          node: {
            borderColor: '#000',
            borderWidth: 1,
          },
        },
        data: this.mindMapData,
      }
    },
  },
  methods: {
    // 设置标签颜色
    addFillProperty(node) {
      if (node.data?.tag) {
        node.data.tag.forEach((tag) => {
          switch (tag.name) {
            case 'redIssuesNum':
              tag.style = {
                fill: '#f56c6c',
              }
              break
            case 'orangeIssuesNum':
              tag.style = {
                fill: '#e6a23c',
              }
              break
            case 'greenNum':
              tag.style = {
                fill: '#67c23a',
              }
              break
            default:
              tag.style = {
                fill: '#1890ff',
              }
          }
        })
      }
      if (node.children) {
        node.children.forEach((child) => this.addFillProperty(child))
      }
    },
    init() {
      this.addFillProperty(this.mindMapData)
      this.mindMap = new MindMap({
        el: document.getElementById('mindMap'),
        ...this.mindMapConfig,
      })
      // 监听数据变化
      this.mindMap.on('data_change', this.mindMapDataChange)
      // 监听历史记录
      this.mindMap.on('back_forward', this.history)
      // 监听节点标签的点击事件
      this.mindMap.on('node_tag_click', this.nodeTagClick)
      // 监听节点右键菜单的点击事件
      this.mindMap.on('node_contextmenu', this.nodeContextMenu)
      // 监听左键点击了画布、左键点击了节点、左键点击了展开收起按钮时、右键点击画布
      this.mindMap.on('node_click', this.nodeContextMenuHide)
      this.mindMap.on('draw_click', this.nodeContextMenuHide)
      this.mindMap.on('expand_btn_click', this.nodeContextMenuHide)
      this.mindMap.on('contextmenu', this.nodeContextMenuHide)
    },
    // 数据变化事件
    mindMapDataChange(data) {
      this.$parent.mindMapData = data
    },
    // 历史记录事件
    history(index, length) {
      this.$parent.$refs.control.undoDisable = index === 0
      this.$parent.$refs.control.redoDisable = index === length - 1
    },
    // 节点标签点击事件
    nodeTagClick(node, item) {
      if (this.isReadonly) {
        return
      }
      this.$emit('nodeTagClick', item)
    },
    // 节点右键菜单点击事件
    nodeContextMenu(e, node) {
      if (this.isReadonly) {
        return
      }
      this.$refs.contextmenu.nodeContextMenu(e, node)
    },
    // 节点右键菜单隐藏事件
    nodeContextMenuHide() {
      if (this.isReadonly) {
        return
      }
      this.$refs.contextmenu.contextmenuShow = false
      this.$refs.contextmenu.currentNode = null
    },
  },
  created() {},
  mounted() {
    this.init()
  },
  beforeDestroy() {
    if (this.mindMap) {
      this.mindMap.off('data_change', this.mindMapDataChange)
      this.mindMap.off('node_tag_click', this.nodeTagClick)
      this.mindMap.off('node_contextmenu', this.nodeContextMenu)
      this.mindMap.off('node_click', this.nodeContextMenuHide)
      this.mindMap.off('draw_click', this.nodeContextMenuHide)
      this.mindMap.off('expand_btn_click', this.nodeContextMenuHide)
      this.mindMap.on('contextmenu', this.nodeContextMenuHide)
      this.mindMap.destroy()
      this.mindMap = null
    }
  },
}
</script>
<style lang="scss" scoped>
#mindMap {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
</style>
